<template>
  <div id="profil">
    <h1>Profil</h1>
    <br />
    <h2>Choisissez un pseudo pour jouer</h2>
    <form id="form">
      <label for="username">Choisir un psuedo</label>
      <input v-model="this.username" id="username" type="text" />
      <button
        @click="updateUsername"
        type="button"
        :disabled="checkValidateForm()"
      >
        Valider
      </button>
    </form>
    <p class="alert">{{alert}}</p>
  </div>
</template>

<script>
const regexUsername =
  /^[a-zA-Z.,'\-àáâäãåąčćęèéêëėįìíîïłńòóôöõøùúûüųūÿýżźñçčšžÀÁÂÄÃÅĄĆČĖĘÈÉÊËÌÍÎÏĮŁŃÒÓÔÖÕØÙÚÛÜŲŪŸÝŻŹÑßÇŒÆČŠŽ∂ð ].{2,9}$/;
export default {
  data() {
    return {
      username: localStorage.getItem("username") ? JSON.parse(localStorage.getItem("username")).username : '',
      alert: ""
    };
  },

  methods: {
    checkValidateForm() {
      if (regexUsername.test(this.username)) {
        return false;
      } else {
        return true;
      }
    },
    updateUsername() {
      localStorage.setItem(
        "username",
        JSON.stringify({ username: this.username })
      );
        this.alert = 'Pseudo Confirmé'
        setTimeout(()=> {
          this.alert = ''
        }, 5000)
    },
  },
};
</script>

<style lang="scss">
#form {
  text-align: center;
  color: white;
  margin: auto;
  display: flex;
  flex-direction: column;
  max-width: 300px;
  width: 100%;
}
  .alert {
    color: green;
    text-align: center;
  }
</style>