<template>
  <div id="home">
    <h1>Liste des salons</h1>
    <div id="list-rooms">
        <h2>Partie en attente de joueur</h2>
      <section class="section">
        <div class="room" v-for="(player, index) in rooms" :key="player">
          <a v-if="player" :href="this.url + `/game/${parseInt(index) + 1}`"
            >Salon n°{{ parseInt(index) + 1 }} -
            {{ getMaxPoints(index) }}K<br />Joueurs
            {{ player !== null ? player : 0 }}/{{ initMaxPlayer(index) }}</a
          >
        </div>
      </section>
        <h2>Partie en 5 000</h2>
      <section class="section">
        <div class="room" v-for="(player, index) in rooms" :key="player">
          <a v-if=" getMaxPoints(index) == 5" :href="this.url + `/game/${parseInt(index) + 1}`"
            >Salon n°{{ parseInt(index) + 1 }}<br />Joueurs
            {{ player !== null ? player : 0 }}/{{ initMaxPlayer(index) }}</a
          >
        </div>
      </section>
              <h2>Partie en 10 000</h2>
      <section class="section">
        <div class="room" v-for="(player, index) in rooms" :key="player">
          <a v-if=" getMaxPoints(index) == 10" :href="this.url + `/game/${parseInt(index) + 1}`"
            >Salon n°{{ parseInt(index) + 1 }}<br />Joueurs
            {{ player !== null ? player : 0 }}/{{ initMaxPlayer(index) }}</a
          >
        </div>
      </section>
        <h2>Salon vide</h2>
      <section class="section">
        <div class="room" v-for="(player, index) in rooms" :key="player">
          <a v-if="!player" :href="this.url + `/game/${parseInt(index) + 1}`"
            >Salon n°{{ parseInt(index) + 1 }} -
            {{ getMaxPoints(index) }}K<br />Joueurs
            {{ player !== null ? player : 0 }}/{{ initMaxPlayer(index) }}</a
          >
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import io from "socket.io-client";
const username = "doudou";

export default {
  name: "Home",
  components: {
    //HelloWorld
  },
  data() {
    return {
      socket: io(window.location.origin),
      url: window.location.origin,
      rooms: [],
    };
  },
  mounted() {
    this.socket.on("home", (list) => {
      this.rooms = list;
    });
    this.socket.emit("home", {
      username: username,
    });
  },
  computed: {},
  methods: {
    initMaxPlayer: function (room) {
      let maxPlayer = Math.floor(parseInt((room - 1) / 5) + 1);
      return maxPlayer < 2 ? 2 : maxPlayer;
    },
    getMaxPoints: function (room) {
      return (room - 1) % 5 < 3 ? 5 : 10;
    },
  },
};
</script>

